import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
//import reportWebVitals from './reportWebVitals';

/*ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
); */

class MyComp extends HTMLElement {
    constructor() {
        super();
        this.observer = new MutationObserver(() => this.update());
        this.observer.observe(this, { attributes: true });
    }
    connectedCallback () {
      this.mount();
    }

    disconnectedCallback () {
        //ReactDOM.unmountComponentAtNode(App)
        this.unmount();
        this.observer.disconnect();
    }

    update() {
        this.unmount();
        this.mount();
    }
    mount() {
        //const props = { ...this.getProps(this.attributes, MyReactComponent.propTypes)};
        //ReactDOM.render(<MyReactComponent {...props} />, this);
        const props = Object.values(this.attributes).map(attribute => [attribute.name, attribute.value])

        ReactDOM.render(React.createElement(App, Object.fromEntries(props)), this)
    }
    unmount() {
        ReactDOM.unmountComponentAtNode(this);
    }
}
customElements.define('test-app', MyComp);

